.title{
    margin-bottom: 30px;
    font-family: 'Chakra Petch';
    font-weight: 400;
    font-size: 20px;
    line-height: 26px;
    color: #fff;
}

.result{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 20px;
    margin-bottom: 90px;
}

.item{
    text-decoration: none;
    background: #1B1B1B;
    border-radius: 10px;
    display: flex;
    align-items: center;
    padding: 20px 5px 20px 20px;
    filter: drop-shadow(4px 4px 4px rgba(60, 60, 62, 0.46));
    color: #fff;

    &Img{
        display: block;
        margin-right: 20px;
    }

    &Name{
        font-family: 'Chakra Petch';
        font-weight: 600;
        font-size: 17px;
        line-height: 22px;
        margin-bottom: 5px;
    }

    &Tags{
        font-weight: 400;
        font-size: 14px;
        line-height: 18px;
    }
}